<template>
	<!-- // 主体区域 -->
	<view>
				<uni-section title="我喜欢的歌手们" type="line" padding>
					<!-- 因为swiper特性的关系，请指定swiper的高度 ，swiper的高度并不会被内容撑开-->
					<swiper class="swiper" :indicator-dots="true">
						<swiper-item>
							<uni-grid :column="3" :highlight="true" @change="change">
								<uni-grid-item v-for="(item, index) in list" :index="index" :key="index">
									<view class="grid-item-box">
										<image :src="item.url" class="image" mode="aspectFill" />
										<text class="text">{{ item.text }}</text>
									</view>
								</uni-grid-item>
							</uni-grid>
						</swiper-item>
						<swiper-item>
							<uni-grid :column="3" :highlight="true" @change="change">
								<uni-grid-item v-for="(item, index) in list" :index="index" :key="index">
									<view class="grid-item-box">
										<image :src="item.url" class="image" mode="aspectFill" />
										<text class="text">{{ item.text }}</text>
									</view>
								</uni-grid-item>
							</uni-grid>
						</swiper-item>
						<swiper-item>
							<uni-grid :column="3" :highlight="true" @change="change">
								<uni-grid-item v-for="(item, index) in list" :index="index" :key="index">
									<view class="grid-item-box">
										<image :src="item.url" class="image" mode="aspectFill" />
										<text class="text">{{ item.text }}</text>
									</view>
								</uni-grid-item>
							</uni-grid>
						</swiper-item>
					</swiper>
				</uni-section>
	</view>
	
</template>

<script>
	export default {
		components: {},
		data() {
			return {
				dynamicList: [],
				list: [{
						url: '/static/singer/zhoujielun.png',
						text: '周杰伦',
						badge: '0',
						type: "primary"
					},
					{
						url: '/static/singer/mowenwei.png',
						text: '莫文蔚',
						badge: '1',
						type: "success"
					},
					{
						url: '/static/singer/chenyixun.png',
						text: '陈奕迅',
						badge: '99',
						type: "warning"
					},
					{
						url: '/static/singer/guofucheng.png',
						text: '郭富城',
						badge: '2',
						type: "error"
					},
					{
						url: '/static/singer/zhangguorong.png',
						text: '张国荣'
					},
					{
						url: '/static/singer/liudehua.png',
						text: '刘德华'
					},
					{
						url: '/static/singer/ladygaga.png',
						text: 'Lady Gaga'
					},
					{
						url: '/static/singer/taylor.png',
						text: 'Taylor Swift'
					},
					{
						url: '/static/singer/justin.png',
						text: 'Justin Bieber'
					}
				],
			
			}
		},
		methods: {
			change(e) {
				let {
					index
				} = e.detail
				this.list[index].badge && this.list[index].badge++

				uni.showToast({
					title: `点击第${index+1}个宫格`,
					icon: 'none'
				})
			},
			add() {
				if (this.dynamicList.length < 9) {
					this.dynamicList.push({
						url: `/static/c${this.dynamicList.length+1}.png`,
						text: `Grid ${this.dynamicList.length+1}`,
						color: this.dynamicList.length % 2 === 0 ? '#f5f5f5' : "#fff"
					})
				} else {
					uni.showToast({
						title: '最多添加9个',
						icon: 'none'
					});
				}
			},
			del() {
				this.dynamicList.splice(this.dynamicList.length - 1, 1)
			}
		}
	}
</script>


